body {
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}


ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
    cursor: pointer;
}

.square.x {
    color: #1ca8fc;
}

.square.o {
    color: #444444;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {

    max-width: 400px;
    margin: 0 auto;
}

.game-inner {
    display: flex;
    flex-direction: row;
}

.game-info {
    margin-left: 20px;
}

.game-options select, .game-options label {
    margin-bottom: 10px;
    margin-right: 10px;
}
